.store {
    min-height: 100vh;
    position: relative;
    display: flex;
    flex-direction: column;

    &__img {
        position: absolute;
        background-image: url('../images/upper-bg.png');
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        top: -50%;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;

        @include media($md) {
            top: -45%;
        }

        @include media($sm) {
            top: -60%;
        }
    }

    &__title {
        margin-top: 5%;
        max-width: 687px;
        width: 100%;
        z-index: 999;

        @include media($md) {
            margin-top: 10%;
            max-width: 500px;
        }

        @include media($xs) {
            margin-top: 5%;
            max-width: 400px;
        }

        @include media($am) {
            margin-top: 10%;
        }

        @include media (460px) {
            max-width: 350px;
        }

        @include media (400px) {
            margin-top: 20%;
            max-width: 280px;
        }
    }

    &__content {
        position: relative;
        flex-grow: 1;
        padding: 15px 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        font-family: "Montserrat";
        color: $white;
    }

    &__jin {
        height: 688px;
        z-index: 999;

        @include media($md) {
            height: 550px;
        }

        @include media($sm) {
            height: 480px;
        }

        @include media($xs) {
            display: none;
        }
    }

    &__bottom-bg_left {
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 0;

        @include media(1920px) {
            width: 65%;
        }

        @include media($xa) {
         //   width: 70%;
        }
    }

    &__bottom-bg_right {
        position: absolute;
        bottom: 0;
        right: 0;
        z-index: 0;

        @include media(1920px) {
            width: 65%;
        }

        @include media($xa) {
           // width: 70%;
        }
    }
}

.cells {
    max-width: 925px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-content: space-between;
    // display: grid;
    // grid-template-columns: repeat(4, 1fr);
    // grid-gap: 40px;
    z-index: 999;

    @include media($lg) {
        max-width: 740px;
    }

    @include media($md) {
        max-width: 660px;
    }

    @include media($sm) {
        max-width: 560px;
    }

    @include media($xs) {
        max-width: $xs;    
    }   
}

.cell {
    margin-bottom: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background: url('../images/playstore/cell-bg.png');
    background-size: cover;
    width: 200px;
    height: 250px;

    @include media($lg) {
        width: 160px;
        height: 200px;
    }

    @include media($md) {
        width: 140px;
        height: 175px;
    }

    @include media($sm) {
        width: 120px;
        height: 150px;
    }

    @include media($xs) {
        width: 150px;
        height: 185px;       
    }

    @include media($xa) {
        width: 110px;
        height: 135px;
    }

    @include media(520px) {
        width: 130px;
        height: 145px;   
    }

    @include media(465px) {
        width: 100px;
        height: 110px;
    }

    @include media(375px) {
        width: 87px;
    }

    &__img {
        object-fit: cover;

        &_watch {
            margin-bottom: -25px;

            @include media($md) {
                margin-bottom: -15px;
            }
        }

        &_wash {
            margin-bottom: -60px;

            @include media($md) {
                margin-bottom: -40px;
            }
        }

        &_vacuumed {
            margin-bottom: -20px;

            @include media($md) {
                margin-bottom: -10px;
            }
        }

        &_iphone {
            margin-bottom: -70px;

            @include media($md) {
                margin-bottom: -40px;
            }
        }
    }

    &__desc-bg {
        object-fit: cover;
        position: absolute;

        @include media($lg) {
            max-width: 220px;
        }

        @include media($md) {
            max-width: 200px;
        }

        @include media($sm) {
            max-width: 175px;
        }

        @include media($xs) {
            max-width: 220px;   
        }

        @include media($xa) {
            max-width: 150px;
            bottom: -10px;
        }

        @include media(520px) {
            max-width: 180px;
            bottom: -15px;
        }

        @include media(465px) {
            max-width: 130px;
        }

        @include media(429px) {
            max-width: 145px;       
        }

        @include media(375px) {
            max-width: 130px;
        }
    }

    &__desc {
        max-width: 220px;
        width: 100%;
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        bottom: 0;
        left: 0;
        margin-bottom: -15px;
        margin-left: -1.5px;
        z-index: 3;


        
        & > p {
            color: #012069;
            text-align: center;
            font-size: 25px;
            font-weight: normal;
            z-index: 4; 

            @include media($lg) {
                font-size: 20px;
            }

            @include media($md) {
                font-size: 17.5px;
            }   

            @include media($sm) {
                font-size: 15px;
            }

            @include media($xs) {
                font-size: 17.5px;
            }

            @include media($xa) {
                height: 22px;
                font-size: 15px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                margin-bottom: -20px;
            }

            @include media(465px) {
                margin-bottom: -45px;
            }
        }
    }
}

.links {
    margin-top: 170px;
    margin-bottom: 10em;

    display: flex;
    justify-content: space-evenly;

    z-index: 5;

    @include media($sm) {
        margin-top: 70px;   
    }

    @include media ($am) {
        margin-top: 40px;
        margin-bottom: 5em;
    }
}

.link {
    display: flex;
    justify-content: center;
    align-items: start;
    flex-direction: column;
    z-index: 5;

    &__google_image {
        width: 100%;
    }

    &__app_image {
        width: 100%;
    }

    &__googlePlay {

        & > p {
            max-width: 690px;
            width: 100%;
        }

        @include media($md) {
            max-width: 380px;
        }

        @include media($xs) {
            max-width: 320px;   
        }

        @include media ($am) {
            max-width: 260px;
        }

        @include media($xa) {
            max-width: 160px;
        }

        @include media(375px) {
            max-width: 140px;
        }
    }

    &__appStore {

        & > p {
            max-width: 370px;
            width: 100%;
        }

        @include media($md) {
            margin-top: -25px;
            max-width: 340px;
        }   

        @include media($xs) {
            max-width: 280px;   
        }

        @include media ($am) {
            max-width: 230px;
        }

        @include media($xa) {
            margin-top: -20px;
            max-width: 140px;
        }

        @include media(375px) {
            max-width: 120px;
        }
    }

    &__desc {
        margin-top: 30px;
        text-align: center;
        font-size: 35px;
        width: 80%;

        @include media($md) {
            margin-top: 15px;
            font-size: 25px;
        }

        @include media($xs) {
            font-size: 19px;   
        }

        @include media($xa) {
            font-size: 15px;
        }

        @include media(375px) {
            font-size: 14px;
        }
    }
}