.offer {
    min-height: 100vh;
    position: relative;
    display: flex;
    flex-direction: column;

    &__img {
        position: absolute;
        background-image: url('../images/upper-bg.png');
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        top: -30%;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;

        @include media($md) {
            top: -45%;
        }

        @include media($sm) {
            top: -60%;
        }
    }

    &__title {
        margin-top: 5%;
        max-width: 687px;
        width: 100%;
        z-index: 999;

        @include media($md) {
            margin-top: 10%;
            max-width: 500px;
        }

        @include media($xs) {
            margin-top: 5%;
            max-width: 400px;
        }

        @include media($am) {
            margin-top: 10%;
        }

        @include media (460px) {
            max-width: 350px;
        }

        @include media (400px) {
            margin-top: 20%;
            max-width: 280px;
        }
    }

    &__content {
        position: relative;
        flex-grow: 1;
        padding: 15px 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        font-family: "Montserrat";
        color: $white;
    }

    &__jin {
        margin-top: -9%;
        height: 688px;
        z-index: 999;

        @include media($md) {
            height: 550px;
        }

        @include media($xs) {
            height: 420px;
        }

        @include media($am) {
            display: none;
        }
    }

    &__bottom-bg_left {
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 0;

        @include media(1920px) {
            width: 65%;
        }

        @include media($xa) {
            width: 70%;
        }
    }

    &__bottom-bg_right {
        position: absolute;
        bottom: 0;
        right: 0;
        z-index: 0;

        @include media(1920px) {
            width: 65%;
        }

        @include media($xa) {
            width: 70%;
        }
    }
}

.content {
    margin-top: 150px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    @include media($sm) {
        margin-top: 80px;
    }

    @include media($am) {
        margin-top: 50px;
    }

    @include media($xa) {
        justify-content: center;
    }

    @include media (460px) {
        margin-top: 30px;
    }

    &__text {
        max-width: 822px;
        width: 100%;
        z-index: 999;

        @include media($xs) {
            max-width: 420px;
        }

        @include media($am) {
            max-width: $am;
        }

        @include media($xa) {
            width: 100%;
        }

        @include media($xa) {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
    }

    &__title {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 155px;
        font-weight: bold;
        background: url('../images/titile-bg.png') center center;
        background-repeat: no-repeat;
        background-size: cover;

        @include media($md) {
            height: 125px;
        }

        @include media($sm) {
            height: 100px;
        }

        @include media($xs) {
            height: 70px;
        }

        @include media($am) {
            height: 80px;
        }

        @include media (460px) {
            height: 68px;
        }

        @include media (400px) {
            height: 53px;
        }

        &>h3 {
            text-align: center;
            max-width: 660px;
            font-size: 44px;
            color: #444444;
            width: 100%;

            @include media($md) {
                font-size: 35px;
            }

            @include media($sm) {
                font-size: 26px;
            }

            @include media($xs) {
                font-size: 22px;
            }

            @include media($am) {
                font-size: 24px;
            }

            @include media (460px) {
                font-size: 20px;
            }

            @include media (400px) {
                font-size: 16px;
            }
        }
    }

    &__offers {
        width: 100%;
        margin-top: 70px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        @include media($sm) {
            margin-top: 50px;
        }

        @include media($xa) {
            flex-direction: column;
            align-items: center;
        }

        @include media (460px) {
            margin-top: 30px;
        }
    }

    &__offer {
        max-width: 385px;
        height: 72px;
        width: 100%;
        background: url('../images/offer-bg.png') center center;
        background-repeat: no-repeat;
        background-size: cover;
        font-size: 35px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;

        @include media($md) {
            width: 45%;
            font-size: 25px;
            height: 63px;
        }

        @include media($sm) {
            font-size: 20px;
            height: 48px;
        }

        @include media($xs) {
            font-size: 18px;
            height: 37px;
        }

        @include media($am) {
            font-size: 20px;
            height: 48px;
        }

        @include media($xa) {
            font-size: 18px;
            height: 68px;
            max-width: 350px;
            width: 100%;
        }

        @include media (460px) {
            height: 53px;
            max-width: 270px;
        }

        @include media (400px) {
            height: 45px;
            max-width: 230px;   
            font-size: 15px;
        }

        &-ru {
            @include media($xa) {
                margin-top: 30px;
            }
        }
    }

    &__policy {
        margin-top: 70px;
        height: 72px;
        background: url('../images/policy-bg.png') center center;
        background-repeat: no-repeat;
        background-size: cover;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        font-size: 35px;

        @include media($md) {
            font-size: 25px;
            height: 63px;
            margin-top: 50px;
        }

        @include media($sm) {
            font-size: 20px;
            height: 50px;
        }

        @include media($xs) {
            font-size: 18px;
            height: 37px;
        }

        @include media($am) {
            font-size: 20px;
            height: 50px;
        }

        @include media($xa) {
            background: url('../images/offer-bg.png') center center;
            background-repeat: no-repeat;
            background-size: cover;
            max-width: 350px;
            width: 100%;
            font-size: 18px;
            height: 68px;
        }

        @include media (460px) {
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            height: 53px;
            max-width: 270px;
            margin-top: 30px;
        }

        @include media (400px) {
            max-width: 230px;
            height: 45px;
            font-size: 15px;
        }
    }

    &__tel {
        margin-top: 70px;
        height: 72px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        font-size: 44px;
        font-weight: bold;

        @include media($md) {
            font-size: 33px;
            margin-top: 50px;
        }

        @include media($xs) {
            font-size: 22px;
        }

        @include media($am) {
            font-size: 24px;
            height: 48px;
        }

        @include media (460px) {
            margin-top: 35px;    
        }

        @include media (400px) {
            font-size: 17px;
        }
    }
}